<template>
    <div id="login">
        <el-form
                :rules="rules"
                ref="loginForm"
                v-loading="loading"
                element-loading-text="正在登录"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :model="loginForm"
                class="loginContainer">
            <h1  style="text-align: center">系统登录</h1>
            <el-form-item  prop="user">
                <el-input  type="text" v-model="loginForm.user" placeholder="请输入用户名" ></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" @keydown.enter.native="submitForm" show-password></el-input>
            </el-form-item>
           <!-- <el-checkbox class="loginRemember" v-model="checked" ></el-checkbox>-->
            <el-button @click="submitForm" type="primary" style="width: 100%;height: 40px;margin-top: 20px " plain >登录</el-button>
        </el-form>
    </div>
    <!--form名字loginForm-->

</template>

<script>
    export default {
        name: "Login",
        data(){
            return{
                loading:false,
                loginForm:{
                    user:'youyuan',
                    password:'123',
                },
                checked:true,
                rules:{
                    user: [{ min: 2,required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{min: 2,required: true, message: '请输入密码', trigger: 'blur'}]
                }
            }
        },
        methods:{
            submitForm() {
                /*获取当前页面所有的ref*/
                this.$refs["loginForm"].validate((valid) => {
                    if (valid) {
                        this.loading=true
                        this.postKeyValueRequest("/doLogin",this.loginForm).then(resp=>{
                            this.loading=false
                            /**
                             * 响应的拦截器已进行二次处理
                             * 如果返回为空，则请求失败
                             * 否者返回服务端的json数据
                             * **/
                            if (resp){
                                //console.log(resp);
                                //登录成功的对象数据保存起来
                                window.sessionStorage.setItem("user",JSON.stringify(resp.obj))
                                //页面跳转到指定页面,通过this.$route.query.redirect获取参数
                                let path = this.$route.query.redirect;
                                this.$router.replace((path=='/' || path == undefined)?"/home":path);
                            }
                        })
                    } else {
                        this.$message.error("请输入账号与密码")
                        return false;
                    }
                });
            },
        }
    }
</script>

<style scoped>

    .loginContainer{
        border-radius: 15px;
        background-clip:padding-box;
        margin: 160px auto;
        width: 380px;
        height: 300px;
        padding: 30px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    .loginRemember{
        margin: 0px 0px 20px 0px;
        text-align: left;
    }
