import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({

    /***
     * 声明变量*/
    state: {
        //存放菜单项
        routes:[],
    },

    mutations: {
        //将查询出来的菜单项赋值给state
        initRoutes(state,data){
            state.routes = data;
        }
    },

    actions: {
    },

    modules: {
    },
})
