//.js文件引入方式
import { Message } from 'element-ui';
import axios from 'axios'
import router from '../router'

/**封装响应
 * 响应的拦截器
 * success回调
 * error回调
 * 失败的情况：
 * 1.业务的失败。如删除失败，服务端的响应还是200，进入success
 * 2.服务端的异常信息，进入error，请求一定失败
 * **/
axios.interceptors.response.use(success=>{
    //console.log(success);
    /**
     * success.data.status 返回的json
     * 业务错误
     * axios的resp返回空，直接判断是否为空，没有则请求失败
     * **/
    if (success.status && success.status ==200 && success.data.status==500){
        Message.error({message:success.data.msg})
        return;
    }
    /**
     * 输出服务端返回的消息
     * **/
    if (success.data.msg){
        Message.success({message:success.data.msg});
    }
    /**
     * 返回json
     * 返回到axios请求调用中
     *
     * **/
    return success.data;

},error=>{     //一定失败
    if (error.response.status == 504){
        Message.error({message:'服务器被吃( ╯□╰ )了'})
    }else if (error.response.status == 404){
        Message.error({message:'服务器找不到您的请求( ╯□╰ )了'})
    }else if (error.response.status == 403){
        Message.error({message:'权限不足，请联系管理员'})
    }else if (error.response.status == 401){
        Message.error({message:'账户已过期，请重新登陆'})
        //返回到登录界面
        router.replace("/");
    }else if (error.response.status == 400){
        Message.error({message:'您的请求错误'});
    }
    else {
        if (error.response.data.msg){
            Message.error({message:error.response.data.msg});
        } else{
            Message.error({message:'未知错误'});
        }
    }
    return;
})


/**
 * 定义请求
 * base 可以统一加前缀
 * transformRequest
 * key->value
 * **/
let base = '';
export const postKeyValueRequest=(url,params)=>{
    return axios({
        method:'post',
        url:`${base}${url}`,    //`` 字符串拼接
        data:params,
        transformRequest:[function (data) {
            let ret = '';
            for (let i in data) {
                //key->value加&
                ret+=encodeURIComponent(i)+'='+encodeURIComponent(data[i])+'&'
            }
            //console.log(ret)
            return ret;
        }],
        headers:{
            'Content-Type':'application/x-www-form-urlencoded'
        }
    })
}

/**
 * GET
 * POST 传递JSON
 * PUT
 * DELETE请求
 * **/

export const postRequest=(url,params)=>{
    return axios({
        method:'post',
        url:`${base}${url}`,
        data:params,
    })
}

export const putRequest=(url,params)=>{
    return axios({
        method:'put',
        url:`${base}${url}`,
        data:params,
    })
}

export const getRequest=(url,params)=>{
    return axios({
        method:'get',
        url:`${base}${url}`,
        data:params,
    })
}

export const deleteRequest=(url,params)=>{
    return axios({
        method:'delete',
        url:`${base}${url}`,
        data:params,
    })
}